.training {
  background-color: #f4f4f9;
  justify-content: space-between;
  gap: 3rem;
  padding: 3rem 5rem 1rem;
  display: flex;
}

.training__text {
  align-self: center;
}

.training__text h1 {
  font-weight: 600;
  display: block;
}

.training__text span {
  display: block;
}

.training__text p {
  width: 85%;
  font-size: .7rem;
  font-weight: 500;
}

.training img {
  width: 30rem;
}

@media screen and (max-width: 650px) {
  .training {
    flex-direction: column;
    padding: 2rem;
  }

  .training__text {
    align-self: start;
  }

  .training img {
    width: 80%;
    height: auto;
    max-width: 90%;
    align-self: end;
  }
}

.course {
  width: 70%;
  text-align: center;
  margin: 3rem auto;
  padding-bottom: 2rem;
}

.course > div {
  position: relative;
}

.course img {
  width: 100%;
}

.course h1, .course h1 span {
  display: block;
}

.course--heading {
  font-size: 2.5rem;
  font-weight: 500;
}

.course__title {
  color: #fff;
  letter-spacing: 3px;
  font-size: 1rem;
  font-weight: 300;
  position: absolute;
  top: 45%;
  right: 30%;
  transform: translate(50%, -50%);
}

.course__title span {
  text-transform: uppercase;
  font-weight: 600;
}

.course h2 {
  letter-spacing: 1px;
}

.course p {
  font-size: .7rem;
  font-weight: 600;
}

@media screen and (max-width: 425px) {
  .course {
    width: 90%;
  }
}

